import React from 'react';

const Kontakt = () => {
  return (
    <div id="Kontakt">
      <p>
        Ak by si sa k nám chcel pridať a pomocť s vývojom, neváhaj a daj o sebe vedieť. Používame
        moderné technológie ako React, JavaScript ES6, Node.js a NoSQL databázu. To všetko beží na
        Heroku cloude a tak máš veľkú šancu sa naučiť čosi nové. Ak to ale už všetko poznáš, môžeš
        naučiť ty nás ako postaviť CestaSNP.sk na nohy rýchlo a efektívne.
      </p>
      <p>
        Kontakt na nás:{' '}
        <a target="_blank" href="mailto:info@cestasnp.sk">
          info@cestasnp.sk
        </a>.
      </p>
      <p>
        CestuSNP.sk nájdeš na{' '}
        <a target="_blank" href="https://github.com/hasty001/cestasnp-web">
          Githube
        </a>.
      </p>
    </div>
  );
};

export default Kontakt;



